
.wrapperCharts {
    display: grid;
    margin: 0 20px;
    grid-template-columns: auto 30em;
    grid-template-rows: auto auto;
    grid-column-gap: 1rem;
    grid-row-gap: 20px;
    grid-template-areas:
        "options options"
        "left right "
}

.optionsChart {
    position: relative;
    grid-area: options;
    display: flex;
    flex-direction: column;

    align-items: center;
    

}

.ChartSelect {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    background: #fff;
    padding: 8px 20px;
    border-bottom:1px solid rgba(29, 62, 78, .8);
    opacity: 1;
    z-index: 100;

}

.leftChartsColumn {
    position: relative;
    grid-area: left;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10px;
    
}

.rightChartsColumn {
    grid-area: right;
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    padding: 0 10px;
   
    background: linear-gradient(to top, #1d3e4e, rgba(29, 62, 78, .8));
}



.lastEventsWrapper {
    position: fixed;
    top: 380px;
    width: 430px;
    padding: 20px;
    
}

.lastEvents {
    display: flex;
    flex-direction: column;

    align-items: center;


    height: 290px;

    color: #fff;

    overflow-y: scroll;
}
.lastEvents::-webkit-scrollbar {
    width: 0.3em;
  }
   
  .lastEvents::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px #ccc;
    border-radius: 8px;
  }
   
  .lastEvents::-webkit-scrollbar-thumb {
    background-color:#fff;
    outline: 1px solid rgba(29, 62, 78, 1);
    border-radius: 8px;
  }
.lastEventsPoint {
    position: relative;
    width: 65%;
    

}

.lastEventsPointV::before {
    content: " ";
    position: absolute;
    left: -8px;
    top: 2.5px;
    width: 4px;
    height: 20px;
    background: rgb(0, 255, 255);
    border-radius: 2px;


}

.lastEventsPointO::before {
    content: " ";
    position: absolute;
    left: -8px;
    top: 2.5px;
    width: 4px;
    height: 20px;
    background: rgb(128, 195, 66);
    border-radius: 2px;


}

.lastEventsPointS::before {
    content: " ";
    position: absolute;
    left: -8px;
    top: 2.5px;
    width: 4px;
    height: 20px;
    background: rgb(209, 216, 89);
    border-radius: 2px;


}